<template>
  <div class="row">
    <div class="col-md-12 card p-0">
      <div class="card-body row p-0">
        <div class="col-sm-12">
          <my-filter :rules="rules" v-on:filter-change="updateFilter"></my-filter>
        </div>
        <div class="col-sm-12">
          <my-table :columnDefs="columnDefs" v-bind:data-rows="menus" :actions="actions" :actionsTable="actionsTable"/>
        </div>
      </div>
    </div>
    <el-dialog title="To menu" :visible.sync="dialogFormCreateVisible" width="30%" @keyup.enter.native="storeMenu">
      <el-form :model="formCreateMenu">
        <el-form-item label="Tiu :">
          <el-input
            autocomplete="off"
            v-model="formCreateMenu.title"
            placeholder="Header, footer, sidebar,..."
            :class="errors.has('name')?'border-danger':''"
            v-validate="'required'"
            data-vv-name="name"
            data-vv-as="Tiu "
          ></el-input>
          <span class="text-danger" v-if="errors.has('name')">{{ errors.first('name') }}</span>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogFormCreateVisible = false">ng</el-button>
        <el-button type="primary" @click="storeMenu" :disabled="formCreateMenu.title?false:true">To menu</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
  import { MessageBox, Message, Dialog, Button, Input, Form, FormItem } from 'element-ui'
  import MyTable from 'src/components/UIComponents/Table.vue'
  import MyFilter from 'src/components/UIComponents/Filter.vue'
  import menuSchemas from './menu-schemas'
  import dtHelper from 'src/helpers/datatable'

  export default{
    components: {
      MyTable,
      MyFilter,
      ElForm: Form,
      ElFormItem: FormItem,
      ElButton: Button,
      ElInput: Input,
      ElDialog: Dialog,
      Message
    },
    computed: {
      menus() {
        let rows = this.$store.state.menus;
        return dtHelper.filterByRules(rows, this.filterOutput);
      }
    },
    data () {
      return {
        filterOutput: [],
        columnDefs: dtHelper.buildColumDefs(menuSchemas),
        actions: [
          {
            type: 'primary',
            icon: 'nc-icon nc-ruler-pencil',
            callback: this.edit
          },
          {
            type: 'danger',
            icon: 'nc-icon nc-simple-remove',
            callback: this.remove
          },
        ],
        filter: {},
        rules: dtHelper.buildRules(menuSchemas),
        dialogFormCreateVisible: false,
        formCreateMenu: {
          title: ''
        },
        actionsTable: [
          {
            title: 'n',
            callback: this.inactiveAll
          },
          {
            title: 'Hin',
            callback: this.activeAll
          },
          {
            title: 'Xa',
            color: 'text-danger',
            callback: this.removeAll
          }
        ],
      }
    },
    mounted() {
      window.AP = this;
      this.$store.dispatch('fetchMenus');

      this.$store.dispatch('setPageTitle', 'Menu');
      this.$store.dispatch('setCurrentActions', [{
        label: 'To menu',
        type: 'primary',
        icon: '',
        callback: this.createMenu
      }])
    },
    methods: {
      edit(index, row){
        this.$router.push('/menus/' + row.id);
      },
      createMenu(){
        this.dialogFormCreateVisible = true;
      },
      storeMenu(){
        let self = this;
        this.$validator.validateAll().then(function (result) {
          if (result) {
            self.$store.dispatch('storeMenu', self.formCreateMenu).then(res => {
              Message({
                message: 'To mi thnh cng',
                type: 'success'
              });
              self.dialogFormCreateVisible = false;
            }, res => {
              Message({
                message: res.message,
                type: 'danger'
              });
            });;
          }
        })
      },
      remove(index, row){
        MessageBox.confirm('Bn c chc chn xa khng?', 'Warning', {
          confirmButtonText: 'ng ',
          cancelButtonText: 'Hy b',
          type: 'warning',
          center: true
        }).then(() => {
          this.$store.dispatch('removeMenu', row.id).then(res => {
            Message({
              type: 'success',
              message: ' xa menu'
            });
          });
        }).catch(() => {
          Message({
            type: 'info',
            message: 'Hy b'
          });
        })
      },
      updateFilter(filterOutput) {
        this.filterOutput = filterOutput;
      },
      inactiveAll(rows){
        this.updateStatus(rows, 'inactive');
      },
      activeAll(rows){
        this.updateStatus(rows, 'active');
      },
      removeAll(rows){
        this.updateStatus(rows, 'delete');
      },
      updateStatus(rows, status){
        let self = this;
        this.$util.updateStatusAll('menu', rows, status).then(function (result) {
          self.$store.dispatch('fetchMenus');
          Message({
            type: 'success',
            message: 'Cp nht thnh cng'
          });
        }).catch(function (error) {
          Message({
            type: 'error',
            message: error.message
          });
        });
      }
    },
    destroyed(){
      this.$store.dispatch('setCurrentActions', []);
    }
  }
</script>
<style lang="scss">
  .el-table .td-actions{
  button.btn {
    margin-right: 5px;
  }
  }
</style>
